<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="3">
          <v-img
            :src="require('../../assets/icons/home-01.png')"
            contain
            height="90"
          />
        </v-col>

        <v-col cols="8" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title">
            Mi abogado LegalHelp
          </h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"
          >Tipo de consulta > Tipo de pago > Dep贸sito bancario</v-col
        >
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">Dep贸sito bancario</div>
          <p>Depositar en estas cuentas</p>

          <div v-for="bank in banks" :key="bank.id" class="call-to-action">
            <v-img :src="bank.iconUrl" contain position="left" width="60" />
            <span>{{ bank.type }}:</span> {{ bank.number }}

            <div style="display: inline-grid; text-align: center">
              <v-btn
                class="mx-2"
                fab
                dark
                small
                color="primary"
                @click="onCopy(bank.number)"
              >
                <v-icon dark> mdi-clipboard-outline</v-icon>
              </v-btn>
              <span>Copiar</span>
            </div>

            <br />
            <span>Titular:</span> {{ bank.principal }}
          </div>

          <div class="text-center" style="margin: 2rem 0">
            <v-btn
              color="primary"
              width="160"
              @click="goToConfirm()"
              :loading="isLoading"
            >
              Continuar
              <v-img
                :src="require('../../assets/icons/lawyer-04.svg')"
                height="20"
                contain
              />
            </v-btn>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import axios from "axios";

export default {
  name: "MyLawyerPayment",
  inject: ["notyf"],
  data: () => ({
    isLoading: false,
    banks: [],
  }),
  computed: {
    ...mapGetters(["user", "services", "service"]),
  },
  created() {
    if (this.service === null) {
      let serviceId = parseInt(this.$route.params.id);
      let service = this.services.find((obj) => obj.id === serviceId);
      this.selectService(service);
    }
  },
  mounted() {
    this.getBankAccounts();
  },
  methods: {
    ...mapActions(["selectService"]),
    goToConfirm() {
      this.isLoading = true;

      let url = "consultations";
      let data = {
        service_id: this.service.id,
        payment_method_id: 2,
        status_id: 1,
        coupon_code: this.service.coupon_code,
      };

      axios
        .post(url, data)
        .then((response) => {
          this.$router.push({
            name: "RequestPayment",
            params: { id: response.data.id },
          });
        })
        .catch(() => {
          this.notyf.error(
            "Ups! Ocurrio un error, por favor intentelo mas tarde."
          );
        })
        .then(() => {
          this.isLoading = false;
        });
    },
    getBankAccounts() {
      axios.get("banks").then((response) => {
        this.banks = response.data;
      });
    },
    onCopy(number) {
      navigator.clipboard.writeText(number);
    },
  },
};
</script>

<style lang="scss" scoped>
.call-to-action {
  span {
    color: #184c72;
  }
}
</style>